@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;900&family=Oswald&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  min-height: 100vh;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Merriweather", serif;
  background-color: #3f3c43;
  background-repeat: no-repeat;
  background-size: cover; }

.site-nav {
  background-color: #903a47;
  display: flex;
  flex-direction: row; }

.navbar-brand {
  display: flex;
  align-items: center; }

.logo {
  height: 60px;
  width: 60px; }

.app-title {
  font-family: "Oswald", sans-serif;
  font-size: 35px;
  color: #d5d5dd; }

.search-bar {
  position: relative;
  width: 480px;
  margin: 240px auto 0;
  padding: 24px 0;
  display: flex;
  justify-content: center;
  border: 5px solid #903a47;
  background-color: #f0b83f; }

.search-bar,
.location {
  border-radius: 10px; }

.units-icon {
  height: 50px;
  width: 50px;
  margin-left: 20px; }

#enter-a-city,
.search-submit {
  font-family: inherit; }

.search-submit,
.results-text {
  color: #d5d5dd; }

.search-submit,
.switch-units {
  cursor: pointer; }

.search-submit {
  margin-left: 12px;
  padding: 5px;
  font-size: 14px;
  font-weight: 700;
  background-color: #903a47;
  border: 1px solid #555;
  border-radius: 5px; }

.forecast {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; }

.forecast-box {
  border: 9px solid #903a47; }

.forecast-icon {
  width: 300px;
  height: 300px; }

.city-name {
  color: #eda812;
  text-shadow: 2px 2px #888; }

.all-places {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.location {
  margin: 10px;
  padding: 0;
  border: 5px solid #f0b83f;
  cursor: pointer; }

.box {
  background-color: #d5d5dd; }

.minor-info {
  display: flex;
  justify-content: space-between; }

.wind {
  margin-top: 25px; }

.left-minor-stat,
.right-minor-stat,
.wind {
  font-weight: 700; }

@media screen and (max-device-width: 375px) {
  .search-bar {
    width: 100vw;
    overflow: none;
    padding: 12px 5px; }

  .units-icon {
    margin-left: 4px; } }

